/// <reference path="../../../../../typings/index.d.ts" />
import { Injectable, Optional } from '@angular/core';
import { Headers, Response, Http } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/Rx';
import * as MSGraph from "microsoft-graph"
import { Broadcaster } from './broadcaster.service';
declare var hello: any;

declare var MicrosoftGraph: any;
@Injectable()
export class O365SecurityService {

    private meUrl: string = "https://graph.microsoft.com/v1.0/me";
    private users: string = "https://graph.microsoft.com/v1.0/users"
    private driveFiles = this.meUrl + "/drive/root/children";
    private instructorSheet = "/drives/b!4T1Mn5DuG0OArpMqWqUOsNJbRaZNMyJMtN2MFg-7S22_y01huc_GR4jWibMwqcvb/items/013W6CYDMH3KYIRPKZXVE3K6OP334T7YXW/workbook/worksheets/Sheet2/"
    private worksheet = "https://graph.microsoft.com/v1.0/me/drive/items/013W6CYDMH3KYIRPKZXVE3K6OP334T7YXW/workbook/worksheets/Sheet1/";
    authToken;




    graphClient = MicrosoftGraph.Client.init({
        debugLogging: true,
        authProvider: (done) => {
            if (typeof this.authToken === "undefined") {
                done({ err: "No auth token" })
            } else {
                done(null, this.authToken);
            }
        }
    });

    constructor(private _http: Http, private _broadcaster: Broadcaster) {
        hello.init({

            aad: {
                name: 'Azure Active Directory',
                oauth: {
                    version: 2,
                    auth: 'https://login.microsoftonline.com/common/oauth2/v2.0/authorize',
                    grant: 'https://login.microsoftonline.com/common/oauth2/v2.0/token'
                },
                scope_delim: ' ',


                // Don't even try submitting via form.
                // This means no POST operations in <=IE9
                form: false
            }
        });

        hello.init(
            {
                aad: "1e71f9e1-9170-4b3c-9138-940cc563ab8a"
            },
            {
                // redirect_uri: "https://localhost:4200",
                 redirect_uri: "https://web2integrators.github.io/WEBCONNECTDEP/",
                scope: "user.readwrite mail.send files.readwrite files.readwrite.all directory.readwrite.all contacts.readwrite files.readwrite.appfolder"
            });
        this.initAuth();
    }

    initAuth() {
        var auth = hello('aad').getAuthResponse();
        if (auth !== null) {
            this.authToken = auth.access_token;
            this._broadcaster.broadcast("login_success", "Done");
        }
    }

    login() {
        hello('aad').login({
            display: 'page',
            state: 'abcd'
        });
    }

    login2() {
        var authenticator = new OfficeHelpers.Authenticator();

        // register Microsoft (Azure AD 2.0 Converged auth) endpoint using
        // authenticator.endpoints.registerMicrosoftAuth('1e71f9e1-9170-4b3c-9138-940cc563ab8a');
        authenticator.endpoints.registerMicrosoftAuth('1e71f9e1-9170-4b3c-9138-940cc563ab8a', {
            redirectUrl: 'https://localhost:4200',
            scope: 'user.readwrite mail.send files.readwrite files.readwrite.all directory.readwrite.all contacts.readwrite files.readwrite.appfolder'
        });
        authenticator
            .authenticate(OfficeHelpers.DefaultEndpoints.Microsoft)
            .then((token) => {
                this.authToken = token.access_token;
                this._broadcaster.broadcast("login_success", "Done");
            })
            .catch(OfficeHelpers.Utilities.log);
    }

    logout() {
        hello('aad').logout();
        this.authToken = undefined;
    }


    get(url: string, select: string, expand: string, filter: string) {
        let result: any;
        result = this.graphClient.
            api(url).
            version("beta").
            select(select).
            expand(expand).
            filter(filter).
            get();
        return Observable.fromPromise(result);
    }

    put(url, param) {
        let result: any;
        result = this.graphClient
            .api(url)
            .version("beta")
            .put(param);
        return Observable.fromPromise(result);
    }

    post(url, param) {
        let result: any;
        result = this.graphClient
            .api(url)
            .version("beta")
            .post(param);
        return Observable.fromPromise(result);
    }

    patch(url, param) {
        let result: any;
        result = this.graphClient
            .api(url)

            .patch(param);
        return Observable.fromPromise(result);
    }

    getPhotos(users: MSGraph.User[]) {

        let arrObservable = Observable.from(users).map((user: MSGraph.User) => {
            this.graphClient.
                api("users/" + user.id + "/photo/$value").
                version("beta").
                responseType('blob').
                get((err, res, rawResponse) => {
                    if (err) throw err;
                    const url = window.URL;
                    const blobUrl = url.createObjectURL(rawResponse.xhr.response);
                    let urlParts  = rawResponse.xhr.responseURL.split('/');
                    this._broadcaster.broadcast("blobUrl", { url: blobUrl, id: urlParts[5] });
                   
                });
        }).subscribe((data: any) => {
            console.log(data);
        })
    }

}




