import { Component } from '@angular/core';
import { DataRouting } from '../../../trinity/services/data.routing.service';
import { Subscription } from 'rxjs/Subscription';
import { Broadcaster } from '../../../shared/services/broadcaster.service';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {

  bool_isRunning: boolean = false;
  sub_spinner_on:Subscription;
  sub_spinner_off:Subscription;
  constructor(private _dataRouting: DataRouting,private _broadcaster:Broadcaster ) {
     this.registerEvents();
  }

  registerEvents() {
    this.sub_spinner_on = this._broadcaster.on<string>('spinner_on')
      .subscribe((result: any) => {
        this.bool_isRunning = result;
      })
       this.sub_spinner_off = this._broadcaster.on<string>('spinner_off')
      .subscribe((result: any) => {
        this.bool_isRunning = result;
      })
  }

}




