import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { MaterialModule } from '@angular/material';

// import {DoubleBounceComponent} from 'ng2-spin-kit/app/spinners';
import { AwesomePipe } from '../pipes/awesome.pipe';
import { HighlightDirective } from '../directives/highlight.directive';
import { Broadcaster } from '../services/broadcaster.service';
import { O365SecurityService } from '../services/o365Security.service';
@NgModule({
  imports: [CommonModule, MaterialModule.forRoot()],
  declarations: [AwesomePipe, HighlightDirective],
  exports: [AwesomePipe, HighlightDirective,
            CommonModule, FormsModule, ReactiveFormsModule, HttpModule, MaterialModule
            ],
  providers: [Broadcaster, O365SecurityService]
})
export class SharedModule { }




