import { Component, Input } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
import { Broadcaster } from '../../../shared/services/broadcaster.service';
import { DataRouting } from '../../services/data.routing.service';
import * as MSGraph from "microsoft-graph"
import * as OfficeHelpers from '@microsoft/office-js-helpers';
import {Authenticator, DefaultEndpoints,Storage} from '@microsoft/office-js-helpers';


@Component({
  templateUrl: './top.component.html',
})
export class TopComponent {
  displayName  : string = "";
  subscription_displayName: Subscription;
  constructor(private _broadcaster: Broadcaster, private _dataRouting: DataRouting) {
   
    this.registerEvents();
  }

  registerEvents()
  {
     this.subscription_displayName = this._broadcaster.on<string>('display.name')
      .subscribe((result: string) => {
        this.displayName = result;
      })
  }

  toggleLeftSideNav() {
    this._broadcaster.broadcast("left_sideNav_toggle", "left");
  }

  login() {
    if(this.displayName)
    {
      this.displayName = "";
      this._dataRouting.logOut();
    }
    else
    {
      this._dataRouting.login();
    }
    
  }
}


