import { Component, Input, OnInit, OnDestroy } from '@angular/core';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { DomSanitizer } from '@angular/platform-browser';
import { ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import { FileUploader } from 'ng2-file-upload';
import { DataRouting } from '../../../services/data.routing.service';
import { Broadcaster } from '../../../../shared/services/broadcaster.service';
import * as MSGraph from "microsoft-graph";
import { Observable } from 'rxjs/Observable';
@Component({
    templateUrl: './dynamicdata.detail.cmp.html',
    styleUrls: ['./dynamicdata.detail.cmp.scss']
})
export class DynamicDataDetailCmp implements OnInit, OnDestroy {

    subscription_get_excel_data: Subscription;
    subscription_patch_excel_data: Subscription;
    excelData: any[] = [];
    message: string = "";
    constructor(private _fb: FormBuilder, private _route: ActivatedRoute, private _broadcaster: Broadcaster, private _DomSanitizationService: DomSanitizer, private _dataRouting: DataRouting) {
    }

    ngOnInit() {
        this.registerEvents();

    }

    ngOnDestroy() {
        this.unRegisterEvents();
    }

    registerEvents() {
        this.subscription_get_excel_data = this._broadcaster.on<string>('get_excel_data')
            .subscribe((result: any) => {
                this.excelData = result;
            })
        this.subscription_patch_excel_data = this._broadcaster.on<string>('patch_excel_data')
            .subscribe((result: any) => {
                this.message = "row saving...";
                if (this.excelData.length - 1 == result.index) {
                    this.message = "Complete...";
                    return;
                }
                else {
                    this.updateExcelRow(this.excelData[result.index + 1], result.index + 1);
                }
            })
    }

    unRegisterEvents() {
        this.subscription_get_excel_data.unsubscribe();
        this.subscription_patch_excel_data.unsubscribe();
    }

    submit() {
        this.updateExcelRow(this.excelData[0], 0);
    }

    updateExcelRow(element, index) {
        let excelUrl = 'drives/b!4T1Mn5DuG0OArpMqWqUOsNJbRaZNMyJMtN2MFg-7S22_y01huc_GR4jWibMwqcvb/items/013W6CYDMH3KYIRPKZXVE3K6OP334T7YXW/workbook/worksheets/Sheet3/tables/2/rows/itemAt(index=' + index + ')';
        var data = {
            "index": index,
            "values": [[element.column0, element.column1, element.column2]]
        }
        this._dataRouting.patch("patch_excel_data", excelUrl, data);
    }

}




