import { Component, Input, OnInit, OnDestroy, NgZone } from '@angular/core';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { DomSanitizer } from '@angular/platform-browser';
import { ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import { FileUploader } from 'ng2-file-upload';
import { DataRouting } from '../../../services/data.routing.service';
import { Broadcaster } from '../../../../shared/services/broadcaster.service';
import { ExcelService } from "../../../services/excel.service"
import * as MSGraph from "microsoft-graph";
import { Observable } from 'rxjs/Observable';
import { InsideOffice } from "../../../../../../app/Modules/shared/utility/officeEnv"
@Component({
    templateUrl: './excel.js.detail.cmp.html',
    styleUrls: ['./excel.js.detail.cmp.scss']
})
export class ExcelJsDataDetailCmp implements OnInit, OnDestroy {

    subscription_get_excel_data: Subscription;
    subscription_patch_excel_data: Subscription;
    subscription_onselection_change: Subscription;
    subscription_ondata_change: Subscription;
    excelData: any[] = [];
    message: string = "";
    currentCellCor: any = {};
    modifiedValue: any;
    isInsideOffice:boolean;
    constructor(private _excelService: ExcelService, private ngzone: NgZone, private _route: ActivatedRoute, private _broadcaster: Broadcaster, private _DomSanitizationService: DomSanitizer, private _dataRouting: DataRouting) {
    }

    ngOnInit() {
        this.registerEvents();
        if (InsideOffice.isOffice) {
            this.isInsideOffice = true;
            this._excelService.init();
        }
    }

    ngOnDestroy() {
        this.unRegisterEvents();
    }

    registerEvents() {
        this.subscription_get_excel_data = this._broadcaster.on<string>('get_excel_data')
            .subscribe((result: any) => {
                this.excelData = result;
            })
        this.subscription_patch_excel_data = this._broadcaster.on<string>('patch_excel_data')
            .subscribe((result: any) => {
                this.message = "row saving...";
                if (this.excelData.length - 2 == result.index) {
                    this.message = "Complete...";
                    return;
                }
                else {
                    this.updateExcelRow(this.excelData[result.index + 1], result.index + 1);
                }
            })

        this.subscription_onselection_change = this._broadcaster.on<string>('SelectionChange')
            .subscribe((selectedCellInfo: any) => {
               
                this.ngzone.run(() => {
                    this.currentCellCor = selectedCellInfo;
                   
                })


            })

        this.subscription_ondata_change = this._broadcaster.on<string>('DataChange')
            .subscribe((message: any) => {
                //  this.alldataChange.push(message);
                this.ngzone.run(() => {
                    this.modifiedValue = "Value Modified " + message;
                })
            })


    }

    unRegisterEvents() {
        this.subscription_get_excel_data.unsubscribe();
        this.subscription_patch_excel_data.unsubscribe();
        this.subscription_onselection_change.unsubscribe();
        this.subscription_ondata_change.unsubscribe();
    }

    submit() {

        this.updateExcelRow(this.excelData[0], 0);

    }

    updateExcelRow(element, index) {
        if(element[0] == "") return;
        let excelUrl = 'drives/b!4T1Mn5DuG0OArpMqWqUOsNJbRaZNMyJMtN2MFg-7S22_y01huc_GR4jWibMwqcvb/items/013W6CYDMH3KYIRPKZXVE3K6OP334T7YXW/workbook/worksheets/Sheet3/tables/2/rows/itemAt(index=' + index + ')';
        var data = {
            "index": index,
            "values": [element]
        }
        this._dataRouting.patch("patch_excel_data", excelUrl, data);
    }

    insertTable() {
        let rows = this.excelData.length;
        let columns = Object.keys(this.excelData[0]).length;
        let newArr: any[] = [];
        this.excelData.map((x) => {
            var arr = Object.keys(x).map((key) => {
                return x[key];
            })
            newArr.push(arr);
        })
        this._excelService.createTable(rows, columns, newArr);
    }

    save()
    {
        this._excelService.getBindingData().then((result:any)=>{
            this.excelData = result;
           this.submit();
        })
    }

}




