    constructor(private broadcasterService: Broadcaster) {


        this.dataChangeObserver = this.dataChangeSubject$
            .asObservable()
            .map((binding: Office.MatrixBinding) => {

                let coordinates = this.allCellChange[this.allCellChange.length - 1];
                if (coordinates == undefined || coordinates == null) {
                    console.log("error");
                    return;
                }
                if (coordinates.rowCount > 1 || coordinates.columnCount > 1) {

                    this.allCellChange.splice(this.allCellChange.length - 1, 1);
                }
                else {

                    if (this.allCellChange.length === 1) {
                        coordinates = this.allCellChange[this.allCellChange.length - 1];
                        this.allCellChange.splice(this.allCellChange.length - 1, 1);
                    }
                    else {
                        coordinates = this.allCellChange[this.allCellChange.length - 2];
                        this.allCellChange.splice(this.allCellChange.length - 2, 1);
                    }
                }

                var options: any = {};
                if (binding.id == "mainTable1") {
                    options.startRow = coordinates.startRow,
                        options.rowCount = coordinates.rowCount,
                        options.startColumn = coordinates.startColumn,
                        options.columnCount = coordinates.columnCount
                }
                else {

                    options.startRow = coordinates.startRow,
                        options.rowCount = coordinates.rowCount
                }

                binding.getDataAsync(options,
                    (asyncResult) => {
                        if (asyncResult.status == Office.AsyncResultStatus.Failed) {
                            // console.log(asyncResult.error);
                            return;
                        }
                        let newdata: any[][] = [[]];
                        newdata.length = 0;
                        if (coordinates.rowCount > 1 && coordinates.columnCount == 1) {
                            for (let i = 0; i < asyncResult.value.length; ++i) {
                                newdata.push([+asyncResult.value[i] * 5]);
                                this.broadcasterService.broadcast("DataChange", +asyncResult.value[i]);
                            }
                        }
                        else if (coordinates.columnCount > 1) {
                            newdata = [[+asyncResult.value.rows[0][0] * 5]];
                            this.broadcasterService.broadcast("DataChange", +asyncResult.value.rows[0][0]);
                            this.allColumnsSelected = 1;
                        }
                        else {
                            newdata = [[+asyncResult.value.rows[0][0]]];
                            //newdata = [[+asyncResult.value[0] * 5]];
                            this.broadcasterService.broadcast("DataChange", newdata);
                        }

                        // this.updateColumns(10, newdata, coordinates.startRow);
                        // this.updateColumns(11, newdata, coordinates.startRow);
                        // this.updateColumns(12, newdata, coordinates.startRow);
                        // this.updateColumns(13, newdata, coordinates.startRow);
                        // this.iscellChange = false;;
                        return newdata;
                    })
            })
            .subscribe(res => {
                console.log(res);
            },
            error => {
                console.log(error);
            });
    }

    updateColumns(biindingIndex: number, newData: any[][], startRow: number): Promise<string> {
        return new Promise((resolve, reject) => {
            this.allBindings[biindingIndex].removeHandlerAsync(Office.EventType.BindingDataChanged, (asyncResult) => {
                if (asyncResult.status == Office.AsyncResultStatus.Failed) {
                    console.log(asyncResult.error);
                }
                else {
                    this.allBindings[biindingIndex].setDataAsync(newData, { startRow: startRow, coercionType: "matrix" }, (asyncResult) => {
                        if (asyncResult.status == Office.AsyncResultStatus.Failed) {
                            console.log(asyncResult.error);
                        }
                        else {
                            this.allBindings[biindingIndex].addHandlerAsync(Office.EventType.BindingDataChanged, this.onDataChange.bind(this), (result) => {
                                if (asyncResult.status == Office.AsyncResultStatus.Failed) {
                                    console.log(asyncResult.error);
                                }
                                else {
                                    resolve("done")
                                }
                            });
                        }
                    })
                }
            });
        })
        // this.allBindings[biindingIndex].removeHandlerAsync(Office.EventType.BindingDataChanged)

        //         this.allBindings[biindingIndex].setDataAsync(newData, { startRow: startRow, coercionType: "matrix" }, (asyncResult) => {

        //                 this.allBindings[biindingIndex].addHandlerAsync(Office.EventType.BindingDataChanged, this.onDataChange.bind(this));

        //         })



    }

