import { Component, Input, OnInit } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import { FileUploader } from 'ng2-file-upload';
import { DataRouting } from '../../../services/data.routing.service';
import { Broadcaster } from '../../../../shared/services/broadcaster.service';
import * as MSGraph from "microsoft-graph";
@Component({
  templateUrl: './instructors.detail.cmp.html',
  styleUrls: ['./instructors.detail.cmp.scss']
})
export class InstructorsDetailCmp implements OnInit {
  public uploader: FileUploader = new FileUploader({});
  private selectedUser: any;
  private success: string = "";
  subscription_photo_upload: Subscription;
  constructor(private route: ActivatedRoute, private _broadcaster: Broadcaster, private _DomSanitizationService: DomSanitizer, private _dataRouting: DataRouting) {
  }
  ngOnInit() {
    this.uploader.onAfterAddingFile = this.selectPhoto.bind(this);
    let id = (this.route.snapshot.params['id']);
    this.selectedUser = this._dataRouting.getUser(id);
    this.registerEvents();
  }

  registerEvents() {
    this.subscription_photo_upload = this._broadcaster.on<string>('photo_upload')
      .subscribe((result: any) => {
        this.success = result;
      })
  }

  selectPhoto(fileItem: any) {
    this.success = "";
    let that = this;
    var reader = new FileReader();
    reader.addEventListener("load", (e: any) => {
      that.selectedUser.url = e.target.result
    }, false);
    if (fileItem && fileItem.some.type.match('image.*')) {
      reader.readAsDataURL(fileItem.some);
    }
  }

  submit() {
    this.success = "";
    let file: any = this.uploader.queue[0];
    let url = "users/" + this.selectedUser.id + "/photo/$value";
    this._dataRouting.put("photo", url, file.some);
    
  }

}




