
import { NgModule } from '@angular/core';

import { TrinityRoutingModule } from './trinity.routing.module';
import { SharedModule } from '../../shared/bootstrap/shared.module';


import { LayoutCmp } from '../cmps/layout/layout.cmp';
import { HomeCmp } from '../cmps/home.cmp';
import { InstructorParent } from '../cmps/instructors/instructorParent.cmp';
import { InstructorsCmp } from '../cmps/instructors/list/instructors.cmp';
import { InstructorsDetailCmp } from '../cmps/instructors/detail/instructors.detail.cmp';
import { StudentDetailCmp } from '../cmps/students/detail/student.detail.cmp';
import { DynamicDataDetailCmp } from '../cmps/dynamicdata/detail/dynamicdata.detail.cmp';
import { ExcelJsDataDetailCmp } from '../cmps/excel-js/detail/excel.js.detail.cmp';
import { LocationsCmp } from '../cmps/locations.cmp';
import { LeftComponent } from '../cmps/left/left.component';
import { TopComponent } from '../cmps/top/top.component';
import { DataRouting } from '../services/data.routing.service';
import { BindingService } from '../services/binding.service';
import { ExcelService } from '../services/excel.service';
import { FileUploadModule } from 'ng2-file-upload';

@NgModule({
  imports: [
    SharedModule,
    TrinityRoutingModule,
    FileUploadModule
  ],
  declarations: [
    HomeCmp, InstructorsCmp, LocationsCmp, LayoutCmp, LeftComponent,
    TopComponent, InstructorParent, InstructorsDetailCmp, StudentDetailCmp,
    DynamicDataDetailCmp, ExcelJsDataDetailCmp
  ],
  providers: [DataRouting, BindingService, ExcelService],
})
export class TrinityModule { }



