import { Component, Input, ViewChild, OnInit } from '@angular/core';
import { MdSidenav } from '@angular/material/sidenav';
import { Router, NavigationEnd, NavigationStart, RoutesRecognized } from "@angular/router"
import { Broadcaster } from '../../../shared/services/broadcaster.service';
@Component({
    templateUrl: './layout.cmp.html'
})

export class LayoutCmp implements OnInit {
    @ViewChild(MdSidenav)
    private sideNav: MdSidenav;
    constructor(private _Broadcaster: Broadcaster, private _router: Router) {
        let url = "layout/(home//left:left//top:top)";
        this._router.navigateByUrl(url);
    }

    ngOnInit() {
        this.registerBroadcast();

    }

    registerBroadcast() {
        this._Broadcaster.on<string>('left_sideNav_toggle')
            .subscribe((sideNav: any) => {
                if (sideNav === "left") {
                    this.sideNav.toggle();
                }
            })

    }

}


