import { Component, Input, OnInit, OnDestroy } from '@angular/core';
import { Router, NavigationEnd, NavigationStart, RoutesRecognized } from "@angular/router"
import { DomSanitizer } from '@angular/platform-browser';
import { DataRouting } from '../../../services/data.routing.service';
import { Broadcaster } from '../../../../shared/services/broadcaster.service';
import * as MSGraph from "microsoft-graph"
import { Subscription } from 'rxjs/Subscription';




@Component({
  templateUrl: './instructors.cmp.html',
  styleUrls: ['./instructors.cmp.scss']
})
export class InstructorsCmp implements OnInit, OnDestroy {
  users: any[] = [];
  subscription_instructorReady: Subscription;
  subscription_blobUrl: Subscription;
  constructor(private _router: Router, private _dataRouting: DataRouting, private _broadcaster: Broadcaster, private _DomSanitizationService: DomSanitizer) {

  }

  ngOnInit() {
    this.registerBroadcast();
  }
  ngOnDestroy() {
    this.unRegisterBroadcast();
  }

  registerBroadcast() {

    this.subscription_instructorReady = this._broadcaster.on<string>('instructorReady')
      .subscribe((photoObj: any) => {
        this.users = this._dataRouting.instructors;
      })
    //todo:what is best way to filter..
    this.subscription_blobUrl = this._broadcaster.on<string>('blobUrl')
      .subscribe((photoObj: any) => {
        this.users.filter((ins) => {
          if (ins.id == photoObj.id) {
            ins.url = photoObj.url;
            ins.photoDone = true;
          }
        })
      })


  }

  unRegisterBroadcast() {
    this.subscription_instructorReady.unsubscribe();

  }

  gotoUser(id) {
    this._router.navigate(['/', id]);
  }

}


