import { Component, ViewChild, OnInit } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
import { Router, NavigationEnd, NavigationStart, RoutesRecognized } from "@angular/router"
import { Broadcaster } from '../../../shared/services/broadcaster.service';
@Component({
  templateUrl: './left.component.html',
})
export class LeftComponent implements OnInit {

  subscription_navLinks: Subscription;
  navItems = []
  constructor(private _router: Router, private _broadcaster: Broadcaster) {
  }

  ngOnInit() {
    this.registerEvents();
  }
  registerEvents() {
    this.subscription_navLinks = this._broadcaster.on<string>('navItems')
      .subscribe((result: any) => {
        this.navItems = result;
      })
  }

  navigate(param) {
    let url = "layout/(primary//left:left//top:top)";
    let newUrl = url.replace('primary', param.route);
    this._router.navigateByUrl(newUrl);
    this._broadcaster.broadcast("left_sideNav_toggle", "left");
  }
}


