import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LayoutCmp } from '../cmps/layout/layout.cmp';
import { HomeCmp } from '../cmps/home.cmp';
import { InstructorParent } from '../cmps/instructors/instructorParent.cmp';
import { InstructorsCmp } from '../cmps/instructors/list/instructors.cmp';
import { InstructorsDetailCmp } from '../cmps/instructors/detail/instructors.detail.cmp';
import { LocationsCmp } from '../cmps/locations.cmp';
import { StudentDetailCmp } from '../cmps/students/detail/student.detail.cmp';
import { DynamicDataDetailCmp } from '../cmps/dynamicdata/detail/dynamicdata.detail.cmp';
import { ExcelJsDataDetailCmp } from '../cmps/excel-js/detail/excel.js.detail.cmp';
import { LeftComponent } from '../cmps/left/left.component';
import { TopComponent } from '../cmps/top/top.component';

export const routes: Routes = [
  {
    path: 'layout', component: LayoutCmp, children: [
      { path: 'home', component: HomeCmp },
      //{ path: 'instructors', component: InstructorsCmp },
      {
        path: 'instructors', component: InstructorParent,
        children: [
          { path: '', component: InstructorsCmp },
          { path: ':id', component: InstructorsDetailCmp }
        ]
      },
      { path: 'register', component: StudentDetailCmp },
      { path: 'data',    component: DynamicDataDetailCmp },
       { path: 'exceljs',    component: ExcelJsDataDetailCmp },
      { path: 'locations', component: LocationsCmp },
      { path: 'left', component: LeftComponent, outlet: "left" },
      { path: 'top', component: TopComponent, outlet: "top" },
    ]
  },

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TrinityRoutingModule { }



